@import 'components/variables';
@import 'components/generales/banner-dudas.scss';

.section-dudas {
  margin-top: -6rem;
}

#seccion-videos {
  button {
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
  }
  .btn-video {
    padding: 0.35rem;
  }
  .v-number {
    background-color: $white;
    border-radius: 50px;
    color: $blue-siroco;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .tab-content {
    width: 100%;
    height: 100%;
    &.tab-videos>.active {
      display: flex;
    }
    &.tab-videos {
      .tab-pane {
        width: 100%;
        height: 100%;
        border: 0.5rem solid #e6e6e6;
        border-radius: 10px;
        align-items: center;
        justify-content: center;
        .btn-play-video {
          width: 20%;
          transition: all 0.3s;
          img {
            width: 100%;
          }
          &:hover {
            transform: scale(1.2);
          }
        }
      }
    }
  }
}
#v-clases-kitesurf-gratis {
  .modal-content {
    background-color: transparent;
    border: none;
    .modal-header {
      border: none;
      .btn-close {
        border-radius: 50px;
        background: #fff url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23F14F78'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
      }
    }
  }
}